.fade-list-enter-active,.fade-list-leave-active{transform:translate3d(30px, 0, 0)}.fade-list-enter,.fade-list-leave-to{transition:all .3s ease;opacity:0}.tree-container{position:relative;color:#515a6e;font-family:microsoft YaHei,Simsun;position:relative;display:inline-block;background:#fff;height:100%;width:100%;text-align:left}.tree-container .tip-box{vertical-align:bottom}.tree-container .tree-body{overflow-x:hidden;overflow-y:auto;position:relative;height:100%;padding:6px 20px 10px 0;min-width:180px}.tree-container .tree-node-container{position:relative}.tree-container .tree-node-container .node-label{border-radius:4px;letter-spacing:1px;padding:2px 4px;margin:2px 0;white-space:nowrap}.tree-container .tree-node-container .node-label-active{color:#088def;background:#d5e8fc}.tree-container .tree-node-container .node-hover:hover{background:#d5e8fc}.tree-container .tree-node-container .label-icon{vertical-align:text-bottom}.tree-container .tree-node-container li,.tree-container .tree-node-container ul{list-style:none}.tree-container .tree-node-container ul{padding-left:20px;width:100%;height:auto}.tree-container .tree-node-container ul li{line-height:24px;font-size:14px}.tree-container .tree-node-container ul li span{display:inline-block;vertical-align:middle}.tree-container .tree-arrow{width:16px;overflow:hidden;text-align:center}.tree-container .tree-arrow:hover{transform:scale(1.2);-webkit-transform:scale(1.2)}.tree-container .tree-arrow img{vertical-align:text-bottom}.tree-container .tree-checkbox{vertical-align:middle}.tree-container .arrow-icon{display:inline-block;font-family:Ionicons;font-size:14px;font-style:normal;font-weight:400;font-variant:normal;text-transform:none;text-rendering:auto;line-height:1;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;vertical-align:text-top;transition:all 0.2s ease-in-out;content:"\F11F"}.tree-container .arrow-down{transform:rotate(90deg);-webkit-transform:rotate(90deg)}.tree-container .point-cursor{cursor:pointer}.tree-container *{margin:0;padding:0;box-sizing:border-box}.tree-container ::-webkit-scrollbar{width:8px}.tree-container ::-webkit-scrollbar-corner{background-color:#dcdcdc}.tree-container ::-webkit-scrollbar-track{box-shadow:inset 0 0 6px #f5f7fa;background:#f5f7fa;border-radius:4px}.tree-container ::-webkit-scrollbar-thumb{border-radius:4px;box-shadow:inset 0 0 6px #dcdcdc;background:#dcdcdc !important}
